<script>
  import Loader from '@/components/Loader.vue';
  import { useCartStore } from "../../store/cart";
  export default {
    components:{
      Loader,
    },
    data() {
      return {
        product: {},
        id: '',
        category: '',
        loader: false,
        cartQty: 0,
      }
    },
    methods: {
      getProduct(){
        const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/product/${this.id}`;
        this.loader = true;
        this.$http.get(api).then((res) => {
          this.product = res.data.product;
          this.loader = false;
        });
      },
      addCart(id) {
        const cartStore = useCartStore();
        cartStore.addItem({id});
      },
      getCategoryClass(category) {
        switch (category) {
          case '入門課程':
            return 'levelOne';
          case '中階課程':
            return 'levelTwo';
          case '高階課程':
            return 'levelThree';
          default:
            return '';
        }
      },
      getTeacherSummary(category) {
        switch (category) {
          case '入門課程':
            return '愛料理主廚有五年的料理經驗，擅長家常料理，用簡單輕鬆的教學方式帶領學員輕鬆進入烹飪世界，讓您愛上烹飪，餐桌上多一道每位菜餚。';
          case '中階課程':
            return '傑森主廚有著經驗豐富的料理教學經驗，現階段在技職學校擔任教師，幽默風趣的上課內容，讓學員上課都開開心心作料理，快跟著傑森主廚一起開心做料理。';
          default:
            return '威力主廚是飯店行政主廚，專長是做台式菜餚，不管是熱炒、家常菜、大菜，威力主廚都非常在行，親切又耐心的個性，廣受學員們好評，威力主廚等您來出好菜。';
        }
      },
      getTeacherName(category) {
        switch (category) {
          case '入門課程':
            return '愛料理 主廚';
          case '中階課程':
            return '傑森 主廚';
          default:
            return '威力 主廚';
        }
      },
    },
    created() {
      this.id = this.$route.params.courseId;
      this.getProduct();
    },
  }
</script>

